body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: white;
  font-family: 'Montserrat', sans-serif;
}
html{
  scroll-behavior: smooth;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* header */
.header{
  width: 100%;
  height: 90px;
  /*background-color: #32729C;*/
  background-color: #0b629cb2;
  backdrop-filter: blur(5px);
  display: flex;
  flex-direction: row;
  align-items: center;
  z-index: 2;
}
.logo-header{
  width: 20%;
  height: 100%;
  background-repeat: no-repeat;
  background-position-x: center;
  background-position-y: center;
  z-index: 1;
}
.logo{
  width: 60px;
  height: 60px;
  margin-right: 100px;
}
.btns{
  width: 80%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.ul-header{
  display: flex;
  padding-left: 0;
}
li ,a{
  list-style: none;
}
a{
  text-decoration: none;
  color: inherit;
}
li:nth-child(6){
  border: 2px solid #F6FF00;
  border-radius: 5px;
  padding: 10px;
}
.more{
  display: none;
  margin-right: 20px;
  z-index: 1;
}



/* landing */
.landing{
  width: 100%;
  height: 100vh;
  position: relative;
  transition: all .5s linear;
  background-position-x: center;
  background-position-y: center;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: left;
  
}
.content-landing{
  width: 765px;
  height: 200px;
  background-color: #0b629cb2;
  margin-left: 30px;
  display: flex;
  justify-content: space-between;
  padding: 40px;
}
.smallfont{
  font-size: 15px;
  text-transform: uppercase;
}
.bigfont{
  font-size: 40px;
  text-transform: uppercase;
}
.btn1{
  width: 120px;
  height: 20px;
  background-color: #F6FF00;
  align-self: flex-end;
  padding: 10px;
  color: #0b629c;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
}

/* half landing */
.half{
  height: 100vh;
  background-position-x: center;
  background-position-y: center;
  background-size: cover;
  position: relative;
  display: flex;
  justify-content: left;
  align-items: center;
}
.over{
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #18649bc5;
  
}
.building{
  z-index: 1;
  position: absolute;
  bottom: 0;
  right: 150px;
  width: 250px;
  height: 600px;
}
.half-talk{
  color: white;
  z-index: 1;
  margin-left: 100px;
  width: 60%;
  height: 250px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-content: left;
}
.half-talk div:nth-child(1){
  font-size: 35px;
  font-weight: bold;
}
.half-talk div:nth-child(2),.half-talk div:nth-child(3){
  /* width: 700px; */
}


/* services */
.services{
  width: 100%;
  height: 75vh;
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.our{
  width: fit-content;
  font-size: 20px;
  font-weight: bolder;
  color: #065992;
}
.items{
  width: 100%;
  display: flex;
  justify-content: left;
  align-self: center;
}
.item{
  width: 20%;
  height: 400px;
  position: relative;
}
.item p{
  position: absolute;
  bottom: 5px;
  left: 20px;
  z-index: 1;
  font-size: 20px;
}
.over-blue{
  position: absolute;
  width: 100%;
  height: 100%;
  bottom: 0;
  background: linear-gradient(
        180deg,
        #ffffff00,
        #ffffff00,
        #32729c8e
    );

}


/* work */
.blue{
  color: #065992;
}
.work{
  font-weight: bolder;
  width: 100%;
  height: 75vh;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: column;
  margin-top: 100px;
}
.photos{
  width: 100%;
  height: 70%;
  display: flex;
  /* grid-template-columns: 60% auto;
  grid-template-rows: 100% auto; */
  flex-direction: row;

}
.many{
  width: 100%;
  height: 100%;
  display: grid;
  grid-gap: 0;
  grid-template-columns: repeat(3, 33.3%);
}
.one{
  width: 50%;
  height: 100%;
  /* display: grid;
  grid-template-columns: 100% auto;
  grid-template-rows: 100% auto; */
  
  position: relative;
}
.one>*{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.i-work{
  background-position: center;
  background-repeat: no-repeat;
  background-size: 120%;
  
}
.over-work{
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 255, 0.212);
  display: flex;
  justify-content: center;
  align-items: center;
  display: none;
}
.view{
  color: white;
  background-color: #065992;
  padding: 10px;
  width: 100px;
  text-align: center;
  font-size: 10px;
  font-weight: 600;
}

/* comments */
.comment{
  width: 100%;
  height: 100vh;
  font-size: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  
}
.comments{
  
  
  display: grid;
  grid-template-columns: repeat(2, 50%);
  width: 70%;
  height: fit-content;
  grid-gap: 10px;
}
.i-comm {
  height: 180px;
  background-color: #15669C;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

}
.head-profile{
  
  height: 30%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  font-weight: 800;
  font-size: 15px;
}
.left-profile{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 40%;
}
.left-profile img{
  width: 30px;
  height: 30px;
}

/* contact */
.contact{
  width: 100%;
  height: 100vh;
  background-color: #065a92e1;
  background-position-x: center;
  background-position-y: center;
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.contact .head-cont p:nth-child(1){
  font-size: 25px;
  font-weight: bold;
}
.head-cont{
  width: 100%;
}
.form,.head-cont{
  width: 35%;
}
form{
  width: 100%;
  height: 100%;
}
.form2{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.form{
  
}
.form2 > input{
  border-radius: 7px;
  border: 0px;
  height: 30px;
}
textarea{
  height: 100px;
  border-radius: 7px;
}
.sub{
  width: 90px;
  background-color: white;
  color: #116ba7;
  border-radius: 20px;
  height: 30px;
  font-weight: 400;
  transform: translateY(10px);
}


/* footer */
.footer{
  height: 50vh;
  background-color: #222222;
  display: grid;
  grid-template-columns: repeat(4, 25%);
  font-size: 13px;
}
.foot1,.foot2,.foot3,.foot4{
  width: 100%;
  height: 100%;
}
.foot2,.foot3,.foot1,.foot4,.foo4{
  display: grid;
}
.foot1,.foot4:nth-child(1),.foot4{
  align-items: center;
  justify-items: center;
}
.links{
  display: flex;
  justify-content: space-between;
  width: 40%;
}
.links img{
  width: 20px;
  height: 20px;
}
.box{
  width: 100px;
  height: 100px;
  background-color: white;
}
.foot2 ul,.foot3 ul{

  height: 60%;
  align-self: center;
  justify-self: left;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.foot2 ul li:nth-child(6),.foot3 ul li:nth-child(6){
  border: 0;
  padding: 0;
}
.foo4{
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  height: 80%;
}
.row{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 120%;
}
.row img{
  width: 20px;
  height: 25px;
}