@media only screen and (max-width : 575px) {
    .header{
        position: relative;
    }

    .content-landing{
        width: 100%;
        flex-direction: column;
        margin-left: 0px;
    }
    .smallfont{
        font-size: 10px;
    }
    .bigfont{
        font-size: 30px;
    }
    .btn1{
        align-self: center;
    }
    .half-talk{
        height: 70%;
        width: 78%;
    }
    .half-talk div:nth-child(2),.half-talk div:nth-child(3){
        width: 300px;
    }

    .services{
        margin-top: 70px;
        height: fit-content;
    }
    .items{
        flex-direction: column;
    }
    .item{
        width: 100%;
        height: 180px;
        background-position: center;
    }

    .photos{
        flex-direction: column;
    }
    .one{
        width: 100%;
    }
    .i-work{
        background-size: 100%;
    }


    .comment{
        height: 110vh;
    }
    .comments{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-evenly;
        height: 100vh;
    }
    .i-comm{
        width: 100%;
        height: 150px;
    }
    .com-wid{
        width: 90%;
    }
    .left-profile{
        width: 50%;
    }


    .form,.head-cont{
        width: 90%;
    }


    .footer{
        grid-template-columns: repeat(1, 100%);
        height: 100vh;
    }
    .foot3 ul{
        justify-self: center;
    }
    .foot2 ul{
        margin-left: 72px;
    }
}
@media only screen and (min-width : 576px) {
    .header{
        position: fixed;
    }
    .services{
        margin-top: 100px;
    }
    .our{
        margin-right: 500px;
    }
    .blue{
        margin-right: 200px;
    }
}
@media only screen and (min-width : 575px) and (max-width : 769px) {
    .half-talk{
        height: 60%;
        width: 80%;
        font-size: 15px;
    }
    
    .half-talk div:nth-child(2),.half-talk div:nth-child(3){
        width: 600px;
    }


    .photos{
        height: 54%;
    }

    /* .form,.head-cont{
        height: 70%;
    } */
    form > p:nth-child(2){
        font-size: 10px;
    }

    .footer{
        grid-template-columns: repeat(2, 50%);
    }
    .foot2 ul,.foot3 ul{
        justify-self: center;
    }
}
@media only screen and (max-width : 776px) {
    .btns{
        display: none;
        position: absolute;
        top: 0;
        left: 0;
        height: 100vh;
        background-color: #32729cc2;
        backdrop-filter: blur(5px);
        display: flex;
        justify-content: center;
        align-self: center;
        width: 100%;
    }
    .btns ul{
        flex-direction: column;
        width: 200px;
        height: 80%;
        justify-content: space-evenly;
        align-items: center;
    }
    .more{
        display: block;
    }
    .header{
        justify-content: space-between;
    }
    .building{
        display: none;
    }
    .half-talk{
        margin: auto;
    }
}
@media only screen and (min-width : 777px){
    .btns{
        width: 80%;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .ul-header{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        width: 800px;
    }
    .our{
        font-size: 30px;
    }

}
@media only screen and (min-width : 850px) and ( max-width : 1100px ) {
    .building{
        right: 0px;
    }
    .half-talk{
        width: 40%;
        height: 350px;
    }
}
@media only screen and (min-width : 1000px){
    .com-wid{
        width: 65%;
    }
}
@media only screen and (min-width : 800px) and (max-width : 999px){
    .com-wid{
        width: 75%;
    }
    .left-profile{
        width: 50%;
    }
}
@media only screen and (min-width : 580) and (max-width : 799px){
    .com-wid{
        width: 80%;
    }
}